

.files-holder .inner-holder{
    display: grid;
    width: 100%;
    gap: 12px;
    min-height: 300px;
    margin-top: 60px;
}


.files-holder .file-tab hr{
    width: 1px;
    height: 32px;
    border: none;
    background-color: rgba(255, 255, 255, 0.233);
    margin: 0;
}

.files-holder .inner-holder .file-tab{
    height: 90px;
    border-radius: 20px;
    border: 1px dashed #05668D;
    background-color: #05668d18;
    display: grid;
    grid-template-columns: max-content max-content auto max-content;
    gap: 40px;
    align-items: center;
    padding: 0 35px;
    color: white;
    font-size: var(--my-medium-font);
}

.files-holder .file-tab button{
    background-color: transparent;
    border-radius: 50px;
    padding: 0.6em 1.5em;
    color: #FF0000;
    border: 1px solid #FF0000;
    background-color: #ff000021;
    font-size: var(--my-medium-font);
}

.files-holder .inner-holder>button{
    font-size: var(--my-medium-font);
    color: white;
    padding: 0.8em 1em;
    display: inline-block;
    border-radius: 50px;
    background-color: #27293C;
    position: relative;
    border: none;
    width: max-content;
    height: max-content;
    margin: auto;
    margin-top: 20px;
}

.files-holder .inner-holder>button input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.001;
}


