

.file-upload{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 500px;
}

.file-upload .file-upload-inner{
    background-color: #EAEAEC;
    border-radius: 30px;
    max-width: 550px;
    min-height: 370px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    padding: 0 24px;

}

.file-upload .icon{
    width: 80px;
    height: 80px;
    background-color: #CBD0DA;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.file-upload h4{
    font-size: 20px;
    margin-bottom: 0;
    margin-top: 60px;
    text-align: center;
}

.file-upload p{
    font-size: 16px;
    text-align: center;
}


.file-upload input{
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0.001;
    width: 100%;
    height: 100%;
}



@media only screen and (max-width: 600px){
    .file-upload .icon{
        width: 70px;
        height: 70px;
    }

    .file-upload h4{
        font-size: 18px;
        margin-top: 40px;
    }
    
    .file-upload p{
        font-size: 14px;
    }
}