:root {
  --my-primary: #05668d;
  --my-color-on-primary: white;
  --my-secondary: red;
  --my-color-on-secondary: red;
  --my-color-surface: #0f0f16;
  --my-color-surface2: #1f202f;
  --my-color-surface3: #2b2d42;
  --my-color-on-surface: #4e5460;

  --my-medium-font: 16px;
  --my-small-font: 14px;
  --my-border-radius: 10px;
  --my-big-font: 32px;
  --my-average-font: 24px;
  --skeleton-color: rgba(255, 255, 255, 0.04);
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.page a {
  text-decoration: none;
}

.page {
  margin-left: calc(50px + 5%);
  margin-right: calc(50px + 5%);
  color: var(--my-color-on-surface);
  font-family: "Raleway";
}

.page h1 {
  color: var(--my-color-on-primary);
}



.page textarea {
  font-family: "Raleway";
}

.page .hover:hover {
  opacity: 0.7;
}


.page h2 {
  font-size: var(--my-big-font);
  margin: 1em 0;
  font-weight: normal;
}

.page h3 {
  font-size: var(--my-average-font);
  margin: 1em 0;
  font-weight: normal;
}

/* .page p {
  font-size: var(--my-medium-font);
  margin: 1em 0;
  line-height: 1.6em;
} */

/* Hide scrollbar for Chrome, Safari and Opera */
.hide-scroll::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.hide-scroll {
  scroll-behavior: smooth;
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}


.page .loading {
  width: 30px;
  height: 30px;
  border: 2px solid #FFF;
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}

.page .loading.dark {
  border-color: #1f202f;
  border-bottom-color: transparent;
}

.page .loading.dark.small {
  width: 24px;
  height: 24px;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media only screen and (max-width: 800px) {
  :root {
    --my-medium-font: 14px;
    --my-border-radius: 8px;
    --my-small-font: 12px;
  }
}

@media only screen and (max-width: 700px) {
  .page {
    margin-left: 24px;
    margin-right: 24px;
  }
}





/* skeleton */


.skeleton {
  background-color: var(--skeleton-color);
  background: linear-gradient(90deg,
      rgba(255, 255, 255, 0) 40%,
      var(--my-color-surface3) 50%,
      rgba(255, 255, 255, 0) 60%) var(--skeleton-color);
  background-size: 200% 100%;
  background-position-x: 180%;
  animation: 1.5s skeleton ease-in-out infinite;
  margin: 16px 0;
}


.card.skeleton{
  width: 100%;
  max-width: 220px;
  min-height: 276px;
}


.heading.skeleton{
  height: 60px;
  width: 100%;
  max-width: 250px;
}

.text.skeleton{
  height: 24px;
  width: 100%;
}

@keyframes skeleton {
  to {
    background-position-x: -20%;
  }
}