.main-content-holder {
  min-height: 500px;
  background-color: var(--my-color-surface2);
  padding-bottom: 40px;
}

.main-content-holder .header,
.tab-outside {
  height: 100px;
  background-color: var(--my-color-surface3);
  display: flex;
  overflow-x: auto;
  gap: 30px;
  align-items: center;
  padding-left: 60px;
  padding-right: 60px;
  white-space: pre;
}

.tab-outside {
  padding: 0;
  justify-content: center;
  background-color: transparent;
}

.main-content-holder .reset-btn {
  margin-left: auto;
  padding: 1em 1.3em;
  border-radius: 50px;
  background-color: rgba(85, 87, 104, 0.2);
  border: none;
  color: white;
  font-size: var(--my-medium-font);
}

.main-content-holder .header .tab,
.tab-outside .tab {
  display: flex;
  align-items: center;
  padding: 1em 1.3em;
  border-radius: 50px;
  background-color: var(--my-color-surface);
  gap: 8px;
  font-size: var(--my-medium-font);
  color: white;
  cursor: pointer;
  min-width: max-content;
}

.tab-outside .tab {
  background-color: rgba(85, 87, 104, 0.2);
}

.main-content-holder .header .tab.active,
.tab-outside .tab.active {
  background-color: var(--my-primary);
}

.main-content-holder .col-2 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  align-items: center;
}

.main-content-holder h2 {
  font-size: 34px;
  color: white;
  margin-top: 0;
  font-weight: normal;
}

.main-content-holder h3 {
  font-size: 24px;
  color: rgba(255, 255, 255, 0.637);
  font-weight: normal;
}

.main-content-holder .content {
  margin: 30px 60px;
}

.main-content-holder .main-col-2 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
}

.main-content-holder input,
.main-content-holder textarea {
  padding: 1em 2em;
  border-radius: var(--my-border-radius);
  border: 1.5px solid #555768;
  background: rgba(85, 87, 104, 0.2);
  font-size: var(--my-medium-font);
  color: #eaeaec;
  width: 100%;
  outline: none;
}

.main-content-holder input {
  height: max-content;
}

.main-content-holder textarea {
  resize: none;
}

.main-content-holder .clean-all {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}

.main-content-holder .clean-all button {
  color: white;
  font-size: var(--my-medium-font);
  border-radius: 50px;
  background-color: rgba(85, 87, 104, 0.2);
  border: none;
  padding: 12px 16px;
  white-space: pre;
}

.main-content-holder .clean-all button.primary {
  background-color: var(--my-primary);
  padding: 12px 16px;
  border-radius: 50px;
}

.main-content-holder .input-title {
  color: #8d99ae;
  font-size: var(--my-medium-font);
  margin-top: 30px;
}
.main-content-holder input:focus,
.main-content-holder textarea:focus {
  border-color: #a0a2ad;
}

.main-content-holder .note {
  color: white;
  display: flex;
  gap: 16px;
  align-items: center;
  line-height: 26px;
  font-size: var(--my-small-font);
}

.main-content-holder .note::before {
  content: "Note";
  background-color: #8d99ae;
  padding: 0.4em 1em;
  border-radius: 40px;
}

.main-content-holder .choose-file {
  display: flex;
  gap: 30px;
  align-items: center;
}

.main-content-holder .choose-file button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 250px;
  height: 64px;
  background-color: #8d99ae;
  font-size: var(--my-medium-font);
  color: white;
  border-radius: var(--my-border-radius);
  border: none;
  outline: none;
}

.main-content-holder .choose-file div {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 62px;
  height: 62px;
  border-radius: var(--my-border-radius);
  background-color: #27293c;
}

.main-content-holder .choose-file > img {
  width: 62px;
  height: 62px;
  border-radius: var(--my-border-radius);
  object-fit: cover;
  display: block;
}

.main-content-holder .chip-holder {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.main-content-holder .chip-holder .chip {
  height: 64px;
  color: white;
  font-size: var(--my-medium-font);
  display: flex;
  gap: 12px;
  align-items: center;
  padding: 0 24px;
  border: 1.5px solid #555768;
  border-radius: var(--my-border-radius);
}

.main-content-holder .chip-holder .chip.active,
.main-content-holder .chip-holder .chip:hover {
  border: 1.5px solid #05668d;
  background-color: rgba(5, 102, 141, 0.2);
}

.main-content-holder .chip-holder .circle {
  background-color: white;
  width: 20px;
  border-radius: 50%;
  height: 20px;
  display: block;
}

.main-content-holder .chip-holder .circle.black {
  background-color: black;
}

.main-content-holder .chip-holder .circle.gray {
  background-color: #555768;
}

.main-content-holder .chip-holder .circle.green {
  background-color: #036666;
}

.main-content-holder .chip-holder .circle.blue {
  background-color: #05668d;
}

.main-content-holder .note-box {
  border-radius: var(--my-border-radius);
  padding: 27px 30px;
  background-color: var(--my-color-surface3);
  line-height: 28px;
  font-size: var(--my-medium-font);
  margin: 24px 0;
}

.main-content-holder .note-box span {
  display: block;
  margin-bottom: 12px;
  color: white;
  font-size: var(--my-medium-font);
}

.main-content-holder .note-box p {
  color: #808b9e;
}

.main-content-holder .paginate {
  display: flex;
  height: 64px;
  gap: 10px;
  padding: 0 24px;
  align-items: center;
  background-color: rgba(85, 87, 104, 0.2);
  border: 1.5px solid #555768;
  width: max-content;
  border-radius: var(--my-border-radius);
}

.main-content-holder .paginate span {
  display: flex;
  width: 40px;
  height: 40px;
  align-items: center;
  justify-content: center;
  background-color: rgba(85, 87, 104, 0.2);
  border-radius: 50%;
  color: white;
  font-size: var(--my-medium-font);
  font-family: "Montserrat";
  font-weight: 600;
  cursor: pointer;
}

.main-content-holder .paginate span.active,
.main-content-holder .paginate span:hover {
  background-color: var(--my-primary);
}

.main-content-holder .temperature {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 24px;
}

.main-content-holder .temperature span {
  color: white;
  display: inline-block;
  padding: 8px 12px;
  background-color: #555768;
  font-family: "Montserrat";
  border-radius: 40px;
}

.main-content-holder .button-holder {
  display: flex;
  justify-content: flex-end;
  gap: 14px;
  align-items: center;
  margin-top: 80px;
}

.main-content-holder button.primary,
.main-content-holder button.secondary {
  color: white;
  border-radius: var(--my-border-radius);
  padding: 0.8em 1em;
  font-size: var(--my-medium-font);
  background-color: #036666;
  border: none;
}

.main-content-holder button.primary:hover,
.main-content-holder button.secondary:hover {
  opacity: 0.7;
}

.main-content-holder button.secondary {
  background-color: #2b2d42;
}

.main-content-holder .checkbox-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.main-content-holder .link-container {
  display: grid;
  grid-template-columns: 1fr max-content;
  gap: 20px;
  align-items: center;
  margin-bottom: 16px;
}

.main-content-holder .link-container input {
  border-radius: 0;
  border-top-left-radius: var(--my-border-radius);
  border-bottom-left-radius: var(--my-border-radius);
}

.main-content-holder .link-container button {
  padding: 1em 2.5em;
  border: none;
  background-color: white;
  color: black;
  font-size: var(--my-medium-font);
  border-top-right-radius: var(--my-border-radius);
  border-bottom-right-radius: var(--my-border-radius);
  display: flex;
  align-items: center;
  gap: 12px;
}

.main-content-holder .link-container button.success {
  background-color: rgb(0, 146, 0);
}

.main-content-holder .link-container button .icon {
  display: flex;
  width: 36px;
  height: 36px;
  align-items: center;
  justify-content: center;
  background-color: #b3bbc9;
  border-radius: 50%;
}

/* media query */

@media only screen and (max-width: 1280px) {
  .main-content-holder .main-col-2 {
    grid-template-columns: 100%;
  }

  .main-content-holder .col-2 {
    grid-template-columns: 100%;
  }
}

@media only screen and (max-width: 900px) {
  .main-content-holder .header {
    height: 100px;
    gap: 20px;
    padding-left: 30px;
    padding-right: 30px;
  }

  .main-content-holder .content {
    margin: 30px;
  }
}

@media only screen and (max-width: 800px) {
  .main-content-holder .link-container {
    grid-template-columns: 100%;
  }

  .main-content-holder .link-container input {
    border-radius: var(--my-border-radius);
  }

  .main-content-holder .link-container button {
    border-radius: var(--my-border-radius);
    justify-content: center;
  }

  /* size change */

  .main-content-holder h2 {
    font-size: 20px;
  }

  .main-content-holder h3 {
    font-size: 18px;
  }

  .main-content-holder .chip-holder {
    flex-wrap: nowrap;
    gap: 12px;
  }


  .main-content-holder .chip-holder .chip {
    height: 54px;
    flex-grow: 1;
    justify-content: center;

  }




  .main-content-holder .choose-file {
    gap: 20px;
  }
  
  .main-content-holder .choose-file button {
    width: 170px;
    height: 54px;

  }
  
  .main-content-holder .choose-file div {
    width: 54px;
    height: 54px;

  }
  
  .main-content-holder .choose-file > img {
    width: 54px;
    height: 54px;
  }




  .main-content-holder .link-container button .icon {

    width: 30px;
    height: 30px;
  }
  
  
}


@media only screen and (max-width: 600px){
  .responsive-hide{
    display: none !important;
  }
}
