
.create-chat{
    background-color: var(--my-color-surface);
    min-height: 400px;
    overflow: auto;
}


.create-chat .header h1{
    font-size: 48px;
    margin-top: 0;
    margin-bottom: 16px;
}

.create-chat .header{
    margin-top: 60px;
    margin-bottom: 60px;
}

.create-chat .header span{
    font-size: var(--my-medium-font);
}

.create-chat .page{
    margin-bottom: 300px;
}



.create-chat .tab-container{
    display: flex;
    /* min-width: max-content; */
    overflow: auto;
    gap: 16px;
    border-bottom: 1px solid #1F202F;
    overflow: auto;
    white-space: pre;
}

.create-chat .tab-container span{
    display: inline-block;
    padding: 1em 1.3em;
    color: #8D99AE;
    font-size: var(--my-medium-font);
    border-bottom: 4px solid transparent;
    cursor: pointer;
}

.create-chat .tab-container span:last-child{
    color: #FF0000 !important;
}

.create-chat .tab-container span.active, .create-chat .tab-container span:hover{
    color: white;
    border-bottom: 4px solid white;
}




@media only screen and (max-width: 800px){
    .create-chat .header h1{
        font-size: 24px;
        margin-bottom: 16px;
    }

    .create-chat .header{
        margin-top: 30px;
        margin-bottom: 30px;
    }
    
}







