
.integration-page{
    background-color: black;
    padding-top: 100px;
    padding-bottom: 100px;

}

.integration-page .page{
    max-width: 800px;
    margin: auto;
}


.integration-page h2{
    color: white;
}

.integration-page p{
    color: rgba(255, 255, 255, 0.8);
}

.integration-page h3{
    margin-top: 3em;
    color: white;
}


.integration-page ul, .integration-page ol{
    color: rgba(255, 255, 255, 0.8);
    font-size: var(--my-medium-font);
    line-height: 1.6em;
}

.integration-page ol{
    padding-left: 24px;
    margin-bottom: 40px;
}

.integration-page li{
    margin-bottom: 1em;
}

.integration-page li a{
    color: white ;
    font-weight: bold;
}


.integration-page li.image{
    list-style-type: none;
}

.integration-page img{
    width: 100%;
    margin-top: 24px;
    margin-bottom: 24px;
}