.simple-dropdown {
  font-size: 20px;
  color: white;
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
}

select {
  appearance: none;
  -webkit-appearance: none;
  cursor: pointer;
  padding: 1em 2em;
  padding-right: 3em;
  border-radius: var(--my-border-radius);
  border: 1.5px solid #555768;
  background-color: rgba(85, 87, 104, 0.2);
  font-size: var(--my-medium-font);
  color: white;
  width: 100%;
  outline: none;
}

select option {
  background-color: #3c3e49;
}

.simple-dropdown::before,
.simple-dropdown::after {
  --size: 0.3rem;
  content: "";
  position: absolute;
  right: 1rem;
  pointer-events: none;
}

