.main-content-holder .flex-wrap {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 30px;
  margin-bottom: 30px;
}

.main-content-holder .flex-wrap button,
.main-content-holder .grid-auto-max-max button {
  color: white;
  font-size: var(--my-medium-font);
  border-radius: var(--my-border-radius);
  background-color: rgba(85, 87, 104, 0.2);
  border: none;
  padding: 16px 18px;
}

.main-content-holder .flex-wrap button.gray,
.main-content-holder .grid-auto-max-max button.gray {
  background-color: #8d99ae;
}

.main-content-holder .grid-auto-max-max {
  display: grid;
  grid-template-columns: 1fr max-content;
  gap: 30px;
  align-items: center;
}

.main-content-holder .grid-auto-max-max .btn-holder{
    display: flex;
    gap: 30px;
    align-items: center;
}

/* chat area */

.main-content-holder .chat-area {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.main-content-holder .chat-area .chat {
  margin-right: 20%;
}

.main-content-holder .chat-area .chat.primary {
  margin-right: 0;
  margin-left: 20%;
  display: flex;
  justify-content: end;
}

.main-content-holder .chat-area .chat p {
  display: inline-block;
  padding: 0.8em 2em;
  border-radius: var(--my-border-radius);
  background-color: white;
  color: black;
  line-height: 2em;
  font-size: var(--my-medium-font);
  margin: 0;
}

.main-content-holder .chat-area .chat.primary p {
  background-color: var(--my-primary);
  color: white;
}

@media only screen and (max-width: 1000px) {
    .main-content-holder .grid-auto-max-max {
        display: grid;
        grid-template-columns: 100%;
        gap: 30px;
        align-items: center;
      }
      
      .main-content-holder .grid-auto-max-max .btn-holder{
          display: grid;
          grid-template-columns: 1fr 1fr;
          gap: 30px;
          align-items: center;
      }
      
}
