.dialog-outer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.452);
  display: flex;
  align-items: center;
  justify-content: center;
}


.dialog {
  max-width: 600px;
  min-height: 400px;
  width: 100%;
  border-radius: 8px;
  background-color: var(--my-color-surface3);
  padding: 24px;
  font-size: var(--my-medium-font);
}

.dialog .title {
  text-align: center;
  margin-top: 0;
  color: white;
}

.dialog p.note {
  background-color: rgba(255, 255, 255, 0.164);
  padding: 16px 12px;
  border-radius: 8px;
  color: rgb(197, 197, 197);
  margin-bottom: 1.5em;
}

.dialog .input-container{
  margin-bottom: 16px;
}
.dialog .input-container p.heading {
  color: rgba(255, 255, 255, 0.596);
  margin: 0;
  margin-top: 8px;
  margin-bottom: 8px;
}

.dialog .input-container .grid-auto-max{
  display: grid;
  grid-template-columns: auto max-content;
  gap: 12px;
  align-items: center;
}

.dialog .input-container .text-field{
  padding: 16px 12px;
  background-color: rgba(255, 255, 255, 0.041);
  border-radius: var(--my-border-radius);
  border: 1px solid rgba(255, 255, 255, 0.151);
  color: white;
  margin: 0;
  font-family: sans-serif;
}

.dialog .input-container .copy-btn{
  background-color: transparent;
  border: none;
  fill: white;
  background-color: rgba(255, 255, 255, 0.075);
  height: 55px;
  width: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: var(--my-border-radius);
}

.dialog .input-container .copy-btn svg{
  fill: white;
}

.dialog .submit-btn{
  margin-left: auto;
  padding: 12px 24px;
  background-color: white;
  color: black;
  border-radius: var(--my-border-radius);
  display: block;
  font-size: var(--my-medium-font);
}




.dialog .status-card{
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid rgba(255, 255, 255, 0.178);
  padding: 12px;
  border-radius: var(--my-border-radius);
}

.dialog .status-card  span{
  color: white;
}

.dialog .status-card .left, .dialog .status-card .right{
  display: flex;
  align-items: center;
  gap: 16px;
}



