@font-face {
  font-family: 'Raleway';
  font-weight: 400;
  font-style: normal;
  src: url('./fonts/static/Raleway-Regular.ttf') format('truetype');
}

@font-face {
  font-family: 'Raleway Bold';
  font-weight: 700;
  font-style: normal;
  src: url('./fonts/static/Raleway-Bold.ttf') format('truetype');
}

@font-face {
  font-family: 'Raleway Bolder';
  font-weight: 900;
  font-style: normal;
  src: url('./fonts/static/Raleway-Black.ttf') format('truetype');
}

@font-face {
  font-family: 'Inter Regular';
  font-weight: 500;
  font-style: normal;
  src: url('./fonts/static/Inter-Regular.ttf') format('truetype');
}

body {
  margin: 0;
  font-family: 'Raleway', 'Helvetica Neue', Helvetica, Arial, sans-serif; /* Multiple fallback font families */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

:root {
  --accent-colour100: rgba(178, 208, 220, 1);
  --accent-colour200: rgba(140, 185, 203, 1);
